.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid var(--color-golden);
  padding: 1rem 0;
  margin: 1rem 0;
}

.cart-item div:first-child {
  flex: 1;
}

.cart-item h2 {
  margin-bottom: 2rem;
  color: var(--color-gray);
  font-family: var(--font-base);
  font-weight: 700;
  text-transform: uppercase;
}

.summary {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 1rem;
}

.price {
  margin: 0;
  margin-right: 2rem;
  font-weight: 700;
  font-family: var(--font-alt);
  color: var(--color-gray);
}

.amount {
  font-weight: 700;
  border: 1px solid var(--color-golden);
  padding: 0.25rem 0.75rem;
  border-radius: 6px;
  font-family: var(--font-alt);
  color: var(--color-gray);
  margin: 0;
}

.actions {
  display: flex;
  flex-direction: row;
}

.cart-item button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: var(--font-alt);
  font-weight: 700;
  font-size: 1.5rem;
  color: var(--color-black);
  border: none;
  outline: none;
  width: 5rem;
  padding: 1rem 1rem;
  text-align: center;
  border-radius: 2px;
  background-color: var(--color-crimson);
  cursor: pointer;
  margin-left: 1rem;
  margin: 0.25rem;
}

.cart-item button:hover,
.cart-item button:active {
  background-color: var(--color-golden);
  color: white;
}

.actions .button--alt {
  color: var(--color-black);
}
.actions .button--alt:hover,
.actions .button--alt:active {
  background-color: var(--color-black);
  color: white;
}

@media screen and (min-width: 2000px) {
  .cart-item button {
    font-size: 2.5rem;
    line-height: 30px;
  }
  .cart-item h2 {
    font-size: 3.5rem;
  }
  .price {
    font-size: 2.5rem;
  }
  .amount {
    padding: 0.5rem 1rem;
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 1550px) {
  .cart-item button {
    font-size: 2rem;
    line-height: 30px;
  }
  .cart-item h2 {
    font-size: 3rem;
  }
  .price {
    font-size: 2rem;
  }
  .amount {
    padding: 0.5rem 1rem;
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 650px) {
  .cart-item button {
    font-size: 2rem;
    line-height: 30px;
  }
  .cart-item h2 {
    font-size: 2.5rem;
  }
  .price {
    font-size: 1.8rem;
  }
  .amount {
    padding: 0.5rem 1rem;
    font-size: 1.3rem;
  }
}
@media screen and (max-width: 450px) {
  .actions {
    flex-direction: column;
  }
}

@media screen and (max-width: 350px) {
  .cart-item button {
    font-size: 1.5rem;
    line-height: 30px;
  }
  .cart-item h2 {
    font-size: 1.8rem;
  }
  .price {
    font-size: 1.5rem;
  }
  .amount {
    padding: 0.3rem 0.7rem;
    font-size: 1.3rem;
  }
}
