.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.75);
}

.modal-overlay {
  position: fixed;
  top: 15vh;
  width: 50rem;
  left: calc(50% - 20rem);

  background: var(--color-white);
  padding: 3rem;
  border-radius: 1px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 20;
}

.modal-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 1rem;
  width: 90%;
}

.slide-down {
  -webkit-animation: slide-down 0.3s ease-out forwards;
  animation: slide-down 0.3s ease-out forwards;
}

@media screen and (min-width: 2000px) {
  .modal-overlay {
    left: 35%;
    width: 80rem;
  }
}
@media screen and (max-width: 1550px) {
  .modal-overlay {
    left: 25%;
    width: 50%;
  }
}
@media screen and (max-width: 900px) {
  .modal-overlay {
    left: 20%;
    width: 60%;
  }
}

@media screen and (max-width: 650px) {
  .modal-overlay {
    left: 5%;
    width: 90%;
  }
}
@media screen and (max-width: 450px) {
  .modal-overlay {
    left: 5%;
    width: 90%;
  }
}

@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
