.cart-items {
  list-style: none;
  margin-bottom: 1rem;
  padding: 0;
  max-height: 25rem;
  overflow: scroll;
}

.total {
  color: var(--color-black);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.3rem;
  margin: 1rem 0;
}

.total span:first-child {
  display: flex;
  justify-content: flex-start;
  margin-left: 0;
  font-size: 2rem;
}

.total span:last-child {
  display: flex;
  justify-content: flex-end;
  margin-right: 0;
  font-size: 2rem;
}

.hidden {
  display: none;
}

.actions {
  text-align: right;
  margin: 2rem 0 0.5rem 0;
}

.actions button:hover,
.actions button:active {
  background-color: #0e3013;
  color: white;
}

.actions .button--alt {
  color: var(--color-black);
}
.actions .button--alt:hover,
.actions .button--alt:active {
  background-color: #5a1a01;
  color: white;
}

.actions .button {
  background-color: var(--color-golden);
  margin-left: 1rem;
  color: white;
}

.order {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.2rem;
  margin: 1rem 0;
}
.button-orderSuccess {
  font: inherit;
  text-align: right;
  cursor: pointer;
  background-color: var(--color-golden);
  color: white;
  padding: 0.5rem 2rem;
  border-radius: 2px;
  border: none;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.616);
  margin-left: 1rem;
}

.button-orderSuccess:hover,
.button-orderSuccess:active {
  background-color: #0e3013;
  border-color: #0e3013;
  color: white;
}

@media screen and (min-width: 2001px) {
  .total,
  .total span:first-child,
  .total span:last-child {
    font-size: 4rem;
  }
}
@media screen and (max-width: 1550px) {
  .total {
    font-size: 3rem;
  }
}

@media screen and (max-width: 650px) {
  .total {
    font-size: 2.5rem;
  }
}
@media screen and (max-width: 450px) {
  .total {
    font-size: 2rem;
  }
}
