.form {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  gap: 1rem;
  margin: 1rem 0;
  height: 22rem;
  overflow: scroll;
}

.control {
  margin-bottom: 1.5rem;
  margin-left: 0;
}

.control label {
  font-family: var(--font-alt);
  color: var(--color-golden);
  font-weight: 700;

  margin-bottom: 0.5rem;
  display: block;
}

.control input {
  font: inherit;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 2px;
  width: 40rem;
  max-width: 100%;
}

.control input::placeholder {
  color: rgba(0, 0, 0, 0.24);
}

.control p {
  font-size: 1.1rem;
}

.actions {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  align-self: center;
  margin-left: 0;
  margin-bottom: 2rem;
}

.actions button {
  font: var(--font-alt);
  color: var(--color-white);
  cursor: pointer;
  background-color: var(--color-golden);
  border: none;
  border-radius: 2px;
  padding: 0.5rem 1rem;
}

.actions button:hover,
.actions button:active {
  background-color: #0e3013;
  color: white;
}

.actions .submit {
  background-color: var(--color-golden);
  margin-left: 1rem;
  color: white;
}

.actions .button--alt {
  color: var(--color-black);
}
.actions .button--alt:hover,
.actions .button--alt:active {
  background-color: #5a1a01;
  color: white;
}

.actions .submit:hover,
.actions .submit:active {
  background-color: #0e3013;
}

.invalid p {
  color: #ca3e51;
}

.invalid input {
  border-color: #aa0b20;
  background-color: #ffeff1;
}

button:disabled,
button:disabled:hover,
button:disabled:active {
  cursor: not-allowed;
  background-color: #ccc;
  color: #3f3f3f;
  border-color: #ccc;
}

@media screen and (min-width: 2000px) {
  .control input {
    width: 35rem;
  }
  .control p {
    font-size: 1.5rem;
  }

  .form {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 1550px) {
  .control input {
    width: 35rem;
  }
}

@media screen and (max-width: 1550px) {
  .control input {
    width: 35rem;
  }
  .control p {
    font-size: 1.3rem;
  }
}
@media screen and (max-width: 1150px) {
  .control input {
    width: 35rem;
  }
}
@media screen and (max-width: 850px) {
  .control input {
    width: 30rem;
  }
}
@media screen and (max-width: 450px) {
  .form {
    grid-template-columns: 1fr;
  }

  .control input {
    width: 100%;
  }
}
